<template>
  <div class="column is-half">
    <div class="card" :class="{ 'not-expanded': !this.expanded }">
      <header class="card-header" @click="toggleCardState">
        <p class="card-header-title">
          {{ title }}
        </p>
        <a class="card-header-icon">
          <svg
            xmlns="http://www.w3.org/2000/svg"
            width="12.828"
            height="7.414"
            viewBox="0 0 12.828 7.414"
            class="icon"
          >
            <g
              id="Group_1670"
              data-name="Group 1670"
              transform="translate(-276.586 -508.224)"
            >
              <g
                id="Group_89"
                data-name="Group 89"
                transform="translate(278 509.638)"
              >
                <path
                  id="Path_141"
                  data-name="Path 141"
                  d="M1933.1,2500.1l-5,5-5-5"
                  transform="translate(-1923.097 -2500.097)"
                  fill="none"
                  stroke="#6951A4"
                  stroke-linecap="round"
                  stroke-linejoin="round"
                  stroke-width="1"
                />
              </g>
            </g>
          </svg>
        </a>
      </header>

      <div class="card-content">
        <div class="content">
          <slot></slot>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    title: {
      type: String,
      default: "",
    },
  },
  data() {
    return {
      expanded: false,
    };
  },
  methods: {
    toggleCardState() {
      this.expanded = !this.expanded;
    },
    closeAll() {
      this.expanded = false;
    },
  },
  created() {},
};
</script>

<style scoped>
.card {
  /* margin-bottom: 10px; */
  width: 100%;
}

.card-header-title {
  font-weight: bold;
}
.card .card-header {
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: space-between;
  /* line-height: 0px !important; */
}
.content {
  border-radius: 10px;
  background: white;
  padding: 10px;
}
.card .card-header .icon {
  transform: rotate(180deg);
  transition: transform 150ms ease-out;
}
.card .card-content {
  transition: all 150ms ease;
}

.card.not-expanded .card-header .icon {
  transform: rotate(0deg);
}
.card.not-expanded .card-content {
  overflow: hidden;
  transform: scaleY(0);
  transform-origin: top;
  opacity: 0;
  height: 0;
  padding: 0;
}
</style>